	module.exports = {"data":{"markdownRemark":{"html":"<p>F君来，拿来文章《王沪宁反对王沪宁》。里面对我好像有一点了解。她说要我帮她提供材料，我说不能提供，因为这里面有道德问题，不能加入这种文章的阵营，否则会变成一种自吹自擂，不论是什么样的资料，均不能。这是一贯的生活原则。不过，F君的这篇东西写得有点像，写出了一般人不易了解的一面。文说：王沪宁也许不是一个均质的整体，而是一个变化的整体，他不断创新自我和消除自我。又说，他是很有理性的人，在任何时候均十分有理性，不会动感情。是吗?</p>\n<p>生活在这个世界上的人：有的是弱者;有的是强者;有的要别人来设定目标，有的给别人设定目标;有的需要感情支持生活，有的需要意志支持生活。我大概在每一对概念中都会选择做后一种人。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0008.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0008.md","frontmatter":{"title":"1月11日 星期二"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0008.md absPath of file >>> MarkdownRemark"}}

